.form,
.form-active {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  font-size: 18px;
  line-height: 24px;
  width: 100%;
  overflow: auto;
  z-index: 12;
  transition: 0.3s;
  transform: translateX(100%);
}

.form-active {
  transform: translateX(0);
}

.form__side-bar {
  width: 34%;
  background: transparent;
  transition: 0.3s;
}

.form__content {
  width: 70%;
  background: $bodyBackgraund;
  overflow: auto;
  padding-bottom: 20px;
}

.form__container {
  padding: 0 16px;
}

.form__header {
  padding-top: 20px;
  margin-bottom: 56px;
}

.form__link-back {
  display: inline-block;
  color: $mainColor;
  position: relative;
  padding-left: 20px;

  & > svg {
    position: absolute;
    left: 0;
    top: calc(50% - 4.5px);
    width: 14px;
    height: 9px;
    pointer-events: none;
  }
}

.form__content-wrapper {
  display: flex;
}

.form__content-left-bar,
.form__content-right-bar {
  width: 50%;
  max-width: 624px;
}

.form__title {
  font-size: 40px;
  line-height: 44px;
  margin-bottom: 32px;
}

.form__text-left-bar {
  width: 100%;
  max-width: 624px;
}

.form__form-name {
  border-bottom: 1px solid $blackColorOpacity-05;
  margin-bottom: 16px;
  padding-bottom: 8px;

  &:nth-child(5),
  &:nth-child(6) {
    padding-bottom: 0;
  }
}

.form__form-name-value {
  font-size: 12px;
  line-height: 12px;
  margin-bottom: 4px;
  color: $blackColorOpacity-05;
}

.form__form-input {
  background: transparent;
  font-size: 18px;
  line-height: 24px;
  width: 100%;
}

.form__input-checkbox input {
  display: none;
}

.wpcf7-list-item-label {
  display: inline-block;
  position: relative;
  margin-right: 2px;
  width: 16px;
  height: 16px;
  border: 1px solid $mainColor;
  transform: translateY(1px);

  &::before {
    content: '';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 16px;
    height: 16px;
    background: url('../images/form/check.svg') center / contain no-repeat;
    transition: opacity 0.2s;
    opacity: 0;
  }
}
.form__input-checkbox span {
  margin-left: 0;
}
.form__input-checkbox input:checked ~ .wpcf7-list-item-label::before {
  opacity: 1;
}

.form__form-politics {
  color: $mainColor;
  border-bottom: 1px solid $mainColor;
  padding-bottom: 1px;
  font-size: 18px;
  line-height: 24px;
}

.form__response {
  display: block;
  width: 100%;
  max-width: 180px;
  border-radius: 36px;
  background: $whiteColor;
  padding: 8px 40px 8px 16px;
  position: relative;
  font-size: 24px;
  line-height: 24px;
  margin-top: 16px;
  color: $mainColor;

  &::before {
    content: '';
    position: absolute;
    right: 17px;
    top: calc(50% - 4.5px);
    width: 14px;
    height: 9px;
    background: url('../images/form/arrow-right.svg') no-repeat;
  }
}

.form__response-block {
  max-width: 464px;
  display: none;
}

.form__response-title {
  font-size: 32px;
  line-height: 40px;
}

.form__dropdown {
  position: relative;
  cursor: pointer;
}

.form__filter-item-select-arrow {
  position: absolute;
  right: 0;
  bottom: 10px;
}

// .form__value1,
// .form__value2 {
//   height: 0;
//   position: absolute;
// }

.form__dropdown-result {
  position: relative;
  pointer-events: none;

  &::before {
    content: '';
    position: absolute;
    right: 0;
    top: calc(50% - 4.5px);
    width: 9px;
    height: 9px;
    background: url('../images/form/arrow-bottom.svg') no-repeat;
  }
}

.form__dropdown-list {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  background: $whiteColor;
  transition: 0.3s;
  padding-top: 8px;
}

.form__dropdown-list-item {
  padding: 4px 8px;
  margin-bottom: 4px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  transition: 0.3s;

  &:hover {
    background: rgba(0, 0, 0, 0.1);
  }
}

.form__dropdown-item-position {
  pointer-events: none;
}

.form__dropdown-item-checked,
.form__dropdown-item-checked-active {
  width: 10px;
  height: 7.5px;
  transition: 0.2s;
  opacity: 0;
  pointer-events: none;
}

.form__dropdown-item-checked-active {
  opacity: 1;
}

.form__dropdown-item-checked-image {
  display: block;
  width: 100%;
}

.form__file-uploader {
  display: flex;
  margin-bottom: 32px;
}

.form__add-link {
  display: inline-block;
  width: 40px;
  height: 40px;
  margin-right: 16px;
  background: transparent;
}

.form__file-uploader-text {
  margin-bottom: 8px;
}

.form__file-uploader-text-correction {
  font-size: 12px;
  line-height: 12px;
  color: $blackColorOpacity-05;
}

.form__information {
  width: 100%;
  display: block;
  background: transparent;
  font-size: 18px;
  line-height: 24px;
}

.form__form-label {
  display: block;
  margin-bottom: 16px;
}

// Styles for 1440px
@media (max-width: 1600px) {
  .form,
  .form__form-input,
  .form__information,
  .form__form-politics {
    font-size: 16px;
    line-height: 20px;
  }

  .form__title {
    font-size: 32px;
    line-height: 40px;
    margin-bottom: 16px;
  }

  .form__response {
    font-size: 20px;
    line-height: 20px;
    max-width: 160px;
  }

  .form__text-left-bar,
  .form__dropdown-result,
  .form__title {
    padding-right: 16px;
  }

  .form__header {
    margin-bottom: 40px;
  }
}

// Styles for 768px
@media (max-width: 991px) {
  .form__container {
    padding: 0 8px;
  }

  .form__side-bar {
    display: none;
  }

  .form__content {
    width: 100%;
  }

  .form__header {
    margin-bottom: 32px;
  }

  .form__content-left-bar {
    width: 35%;
  }

  .form__content-right-bar {
    width: 65%;
  }

  .form__response {
    font-size: 18px;
    line-height: 18px;
    max-width: 147px;
  }
}

// Styles for 320px
@media (max-width: 650px) {
  .form,
  .form__form-input,
  .form__information,
  .form__form-politics {
    font-size: 14px;
    line-height: 16px;
  }

  .form__title {
    font-size: 24px;
    line-height: 28px;
  }

  .form__content-wrapper {
    flex-direction: column;
  }

  .form__content-left-bar,
  .form__content-right-bar {
    width: 100%;
  }

  .form__content-left-bar {
    margin-bottom: 24px;
  }

  .form__text-left-bar {
    display: inline;
    padding-right: 0;
  }

  .form__file-uploader {
    margin-bottom: 16px;
  }

  .form__header {
    margin-bottom: 24px;
  }

  .form__response-title {
    font-size: 24px;
    line-height: 28px;
  }
}

.form__file-uploader .wpcf7-form-control-wrap.upload-file-803 {
  width: 100%;
}
.cd-upload-btn {
  color: #000;
}

.dnd-upload-status .dnd-upload-details .dnd-progress-bar span {
  background: #969696;
}

.name > span {
  color: $mainColor;
}

.codedropz-upload-handler {
  border: 1px dashed #969696;
}

.form__file-uploader {
  margin-bottom: 10px;
}

.form__content-right-bar-100 {
  width: 100%;
}

a.cd-upload-btn {
  color: rgba(0, 0, 0, 0.5);
  font-family: Suisse;
  font-size: 12px;
  line-height: 12px;
  text-decoration: underline;
}

.codedropz-upload-inner span {
  display: none;
}
.codedropz-upload-inner h3 {
  font-family: suisse;
  // font-size: 16px;
  // line-height: 20px;
  color: #000000;
}
input#form__town {
  display: none;
}
