// @import './styles.scss';

.intern {
  margin-bottom: 96px;
  font-size: 18px;
  line-height: 24px;
  overflow: hidden;
}

.intern__header {
  margin-bottom: 192px;
  position: relative;
}

.intern__header-background {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  max-width: 907px;
  //background: url('../images/internship/swichers.svg') right / contain no-repeat;
  //padding-top: 15%;
}

.intern__title {
  font-size: 160px;
  line-height: 144px;
  letter-spacing: -0.03em;
  max-width: 1163px;
  margin-bottom: 48px;
}

.intern__header-text {
  max-width: 640px;
  margin: 0 auto;
}

.intern__header-link {
  position: absolute;
  left: 53%;
  bottom: 21%;
  width: 260px;
  background: #ffffff;
  box-shadow: 16px 2px 24px rgba(0, 0, 0, 0.03);
  border-radius: 36px;
  transform: rotate(-2deg);
  font-size: 24px;
  line-height: 24px;
  text-transform: lowercase;
  padding: 9px 40px 9px 16px;
  color: $mainColor;
  transition: 0.3s;

  & > svg {
    position: absolute;
    right: 17px;
    top: calc(50% - 3px);
    width: 14px;
    height: 9px;
    pointer-events: none;
  }

  &:hover {
    transform: rotate(0);
  }
}

.intern__main-content {
  display: flex;
  justify-content: space-between;
}

.intern__events-block {
  width: 28.33%;
}

.intern__events-block-mobile {
  display: none;
}

.intern__events-title,
.intern__events-group {
  margin-bottom: 56px;
}

.intern__events-group-month {
  margin-bottom: 44px;
}

.intern__event {
  padding: 32px 0;
  border-bottom: 1px solid $blackColorOpacity-05;
  display: flex;
  justify-content: flex-start;
}

.intern__event-date-block {
  width: 39.15%;
  margin-right: 16px;
}

.intern__event-date {
  font-size: 160px;
  line-height: 144px;
  letter-spacing: -0.03em;
  font-feature-settings: 'tnum' on, 'lnum' on, 'zero' on;
}

.intern__event-description-block {
  display: flex;
  flex-direction: column;
  width: 57.9%;
}

.intern__event-method {
  color: #969696;
}

.intern__event-link,
.intern__events-all-link,
.intern__news-link {
  margin-top: auto;
  color: $mainColor;
  width: 100%;
  max-width: 202px;
  display: block;
  position: relative;

  &::after {
    content: '';
    position: absolute;
    right: 0;
    top: calc(50% - 4.5px);
    width: 13px;
    height: 9px;
    background: url('../images/internship/arrow-right.svg') center / contain
      no-repeat;
  }
}

.intern__events-all-link {
  max-width: 132px;
}

.intern__news-link {
  max-width: 129px;
}

// .intern__news-block {
//   width: 65.83%;
// }

.intern__news-block-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
}

.intern__main-news {
  margin-bottom: 56px;
}

.intern__main-news-image-block {
  margin-bottom: 8px;
}

.intern__main-news-image {
  display: block;
  width: 100%;
}

.intern__main-news-title {
  font-size: 40px;
  line-height: 44px;
  margin-bottom: 8px;
}

.intern__subnews-block {
  display: flex;
  justify-content: space-between;
}

.d-flex {
  display: flex;
}
.flex-direction-column {
  flex-direction: column;
}
.ml-30 {
  margin-left: 30px;
}
.intern__news {
  width: 49.4%;
}

.intern__news-image-block {
  margin-bottom: 12px;
}

.intern__news-image {
  display: block;
  width: 100%;
}

.intern__news-title {
  max-width: 464px;
  margin-bottom: 32px;
}

