.svg-toggle {
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 950px;
  overflow: hidden;
  padding: 10px;
}
.svg-toggle-line {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 950px;

  &_768 {
    display: none;
  }

  &_320 {
    display: none;
  }
}

.svg-toggle-line:nth-child(2) {
  max-width: 800px;
  top: 20px;
  left: 262px;
  margin-bottom: 15px;
}

.svg-toggle__btn {
  position: relative;
  // display: flex;
}

