.intern-no-events {
  font-size: 18px;
  line-height: 24px;
}

.intern-no-events-container {
  padding: 0 16px;
}

.intern-no-events__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 32px;
}

.intern-no-events__news-link,
.intern-no-events__all-news-link {
  color: $mainColor;
  position: relative;
}

.intern-no-events__all-news-link {
  padding-right: 20px;

  & > svg {
    position: absolute;
    right: 0;
    top: calc(50% - 3px);
    width: 14px;
    height: 9px;
    pointer-events: none;
  }
}

.intern-no-events__news-block {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 56px;

  &:last-child {
    margin-bottom: 0;

    & > .intern-no-events__news-card-second {
      order: -1;
    }
  }
}

.intern-no-events__news-card-main {
  width: 63%;
  color: #000000;

  & > .intern-no-events__title {
    font-size: 40px;
    line-height: 44px;
    margin-bottom: 8px;
    max-width: 960px;
  }
}

.intern-no-events__card-image-block {
  margin-bottom: 12px;
}

.intern-no-events__card-image {
  display: block;
  width: 100%;
  height: auto;
}

.intern-no-events__news-card-second {
  width: 35%;
  color: #000000;

  & > .intern-no-events__title {
    font-size: 18px;
    line-height: 24px;
    max-width: 464px;
    margin-bottom: 32px;
  }
}

// Styles block widthout events for 1440px
@media (max-width: 1440px) {
