  }

  .intern-no-events__header {
    margin-bottom: 24px;
  }

  .intern-no-events__news-card-main {
    & > .intern-no-events__title {
      font-size: 32px;
      line-height: 40px;
      max-width: 100%;
    }
  }

  .intern-no-events__news-block {
    margin-bottom: 40px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  .intern-no-events__news-card-second {
    & > .intern-no-events__title {
      font-size: 16px;
      line-height: 20px;
      margin-bottom: 24px;
      max-width: 100%;
    }
  }

  //   . 

  .intern__header-background {
    max-width: 747px;
    overflow: hidden;
  }

  .intern__outside-oval {
    transform: scale(0.75) translateX(-45px);
  }

  .intern__inside-circle {
    transform: scale(0.75);
    left: -3px;
    top: 10px;
  }

  .intern__inside-circle-active {
    left: 95px;
  }

  .svg-toggle-line {
    height: 100px;
  }

  .svg-toggle-line:nth-child(2) {
    justify-content: flex-start;
    left: 193px;
  }

  .svg-toggle__btn {
    width: 204px;
  }

  .svg-toggle__btn,
  .svg-toggle__arrow-container {
    margin-right: 16px;

    &:last-child {
      margin-right: 0;
    }
  }

  .svg-toggle__arrow-container {
    width: 120px;
    height: 24px;
    overflow: hidden;
  }

  .svg-toggle__arrow {
    transform: scale(0.75) translateX(-37px);
  }
  //   . 
}

